@import url(https://fonts.googleapis.com/css?family=Roboto+Slab&display=swap&subset=latin-ext);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  text-align: center;
  overflow: hidden;
  font-family: Roboto Slab;
}

.app {
  -webkit-animation: roll 60s alternate infinite linear, appear 4s;
          animation: roll 60s alternate infinite linear, appear 4s;
  background-position: 0 0;
  background-size: cover;
  height: 150vh;
}

.overlay {
  background-color: rgba(255, 255, 255, 0.3);
  -webkit-animation: pulse 10s infinite linear alternate;
          animation: pulse 10s infinite linear alternate;
  height: 150vh;
}

p {
  margin: 0;
  font-size: 2.5vh;
  opacity: 0;
}

.fieldName,
.active {
  opacity: 1;
  -webkit-animation: appear 1s;
          animation: appear 1s;
}

h1 {
  margin: 0;
  padding: 2vh;
  -webkit-animation: icelandicFlag 6s 5s infinite linear forwards, ascend 2s both;
          animation: icelandicFlag 6s 5s infinite linear forwards, ascend 2s both;
}

h3 {
  margin: 0;
  padding: 2vh;
  font-size: 3.5vh;
  -webkit-animation: ascend 1s 2s both;
          animation: ascend 1s 2s both;
}

#form {
  -webkit-animation: ascend 0.5s 3s both;
          animation: ascend 0.5s 3s both;
}

input {
  height: 5vh;
  width: 70vw;
  border: 0.6vh solid black;
  background-color: transparent;
}

.fieldName {
  font-size: 3vh;
  font-weight: bold;
}

button {
  background-color: transparent;
  padding: 2vh;
}

.submit {
  width: 70vw;
  border: 0.6vh solid black;
  margin: 2vh;
  font-size: 3vh;
  font-weight: bold;
  -webkit-animation: ascend 0.5s 4s both;
          animation: ascend 0.5s 4s both;
}

.langs {
  -webkit-animation: ridein 2s 4s both;
          animation: ridein 2s 4s both;
}

.langs button {
  height: 8vh;
  padding: 0;
  overflow: hidden;
  margin: 1vh;
  width: 8vh;
  border-radius: 50%;
  border: 0.4vh solid black;
  cursor: pointer;
}

img {
  height: 100%;
  z-index: -1;
}

#sw img {
  -webkit-transform: translate(-15%, 0);
          transform: translate(-15%, 0);
}
#no img {
  -webkit-transform: translate(-10%, 0);
          transform: translate(-10%, 0);
}
#en img {
  -webkit-transform: translate(-25%, 0);
          transform: translate(-25%, 0);
}

.ok {
  -webkit-animation: ascend 5s;
          animation: ascend 5s;
  font-size: 5vh;
  position: fixed;
  top: 30%;
}

@-webkit-keyframes roll {
  from {
    background-position: 0 0;
  }

  to {
    background-position: 100% 0;
  }
}

@keyframes roll {
  from {
    background-position: 0 0;
  }

  to {
    background-position: 100% 0;
  }
}

@-webkit-keyframes appear {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes appear {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@-webkit-keyframes pulse {
  from {
    background-color: rgba(255, 255, 255, 0.3);
  }
  to {
    background-color: rgba(255, 255, 255, 0.7);
  }
}

@keyframes pulse {
  from {
    background-color: rgba(255, 255, 255, 0.3);
  }
  to {
    background-color: rgba(255, 255, 255, 0.7);
  }
}

@-webkit-keyframes icelandicFlag {
  0% {
    color: white;
  }
  25% {
    color: blue;
  }
  50% {
    color: white;
  }
  75% {
    color: red;
  }
  100% {
    color: white;
  }
}

@keyframes icelandicFlag {
  0% {
    color: white;
  }
  25% {
    color: blue;
  }
  50% {
    color: white;
  }
  75% {
    color: red;
  }
  100% {
    color: white;
  }
}

@-webkit-keyframes ridein {
  from {
    -webkit-transform: translate(-300%, 0);
            transform: translate(-300%, 0);
  }
  to {
    -webkit-transform: none;
            transform: none;
  }
}

@keyframes ridein {
  from {
    -webkit-transform: translate(-300%, 0);
            transform: translate(-300%, 0);
  }
  to {
    -webkit-transform: none;
            transform: none;
  }
}

@-webkit-keyframes ascend {
  from {
    opacity: 0;
    -webkit-transform: translate(0, 100%);
            transform: translate(0, 100%);
  }
  to {
    opacity: 1;
    -webkit-transform: none;
            transform: none;
  }
}

@keyframes ascend {
  from {
    opacity: 0;
    -webkit-transform: translate(0, 100%);
            transform: translate(0, 100%);
  }
  to {
    opacity: 1;
    -webkit-transform: none;
            transform: none;
  }
}

@media (orientation: landscape) {
  .main {
    width: 40vw;
    margin-left: 10vw;
    height: 100vh;
  }
  .app {
    -webkit-animation: none;
            animation: none;
    background-position: center;
  }

  .overlay {
    background-color: rgba(255, 255, 255, 0.3);
    -webkit-animation: pulse 10s infinite linear alternate;
            animation: pulse 10s infinite linear alternate;
    height: 150vh;
  }

  input,
  .submit {
    width: 70%;
    background-color: rgba(0, 0, 0, 0.4);
    -webkit-transition: 0.3s;
    transition: 0.3s;
  }

  .submit:hover {
    cursor: pointer;
    background-color: white;
  }
}

.intro {
  height: 100vh;
  width: 100vw;
}

.flag {
  height: 36vh;
  width: 50vh;
  margin: 0 auto;
  position: fixed;
  top: 50vh;
  left: 50vw;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  animation: appear 2s 6s reverse both;
}

.flag div {
  height: 4vh;
  width: 4vh;
  position: absolute;
}

.white {
  background-color: #fff;
}

.white.Y {
  -webkit-animation: whiteY 0.5s 4s both ease;
          animation: whiteY 0.5s 4s both ease;
  height: 36vh;
}

.white.X {
  -webkit-animation: whiteX 0.5s 4s both ease;
          animation: whiteX 0.5s 4s both ease;
  -webkit-transform-origin: center;
          transform-origin: center;
  width: 50vh;
}

.flag .blue {
  background-color: #00519d;
  height: 36vh;
  -webkit-animation: blue 1s 5s both ease;
          animation: blue 1s 5s both ease;
  -webkit-transform-origin: left;
          transform-origin: left;
}

.red.Y {
  background-color: #dd1832;
  -webkit-animation: redY 4s both ease;
          animation: redY 4s both ease;
  /* transform: translate(16vh, 16vh); */
}

.red.X {
  background-color: #dd1832;
  -webkit-animation: redX 4s both ease;
          animation: redX 4s both ease;
}

@-webkit-keyframes redY {
  0% {
    -webkit-transform: translate(16vh, -200vh) rotate(0deg);
            transform: translate(16vh, -200vh) rotate(0deg);
  }
  30% {
    background-color: #00519d;
  }
  40% {
    -webkit-transform: translate(16vh, 16vh) rotate(720deg);
            transform: translate(16vh, 16vh) rotate(720deg);
    background-color: #dd1832;
  }
  100% {
    -webkit-transform: translate(16vh, 16vh) scaleY(9);
            transform: translate(16vh, 16vh) scaleY(9);
  }
}

@keyframes redY {
  0% {
    -webkit-transform: translate(16vh, -200vh) rotate(0deg);
            transform: translate(16vh, -200vh) rotate(0deg);
  }
  30% {
    background-color: #00519d;
  }
  40% {
    -webkit-transform: translate(16vh, 16vh) rotate(720deg);
            transform: translate(16vh, 16vh) rotate(720deg);
    background-color: #dd1832;
  }
  100% {
    -webkit-transform: translate(16vh, 16vh) scaleY(9);
            transform: translate(16vh, 16vh) scaleY(9);
  }
}

@-webkit-keyframes redX {
  0% {
    -webkit-transform: translate(-200vh, 16vh) rotate(0deg);
            transform: translate(-200vh, 16vh) rotate(0deg);
    background-color: #00519d;
  }
  30% {
    background-color: #00519d;
  }
  40% {
    background-color: #dd1832;
  }
  40% {
    -webkit-transform: translate(16vh, 16vh) rotate(720deg);
            transform: translate(16vh, 16vh) rotate(720deg);
  }
  100% {
    -webkit-transform: translate(23vh, 16vh) scaleX(12.5);
            transform: translate(23vh, 16vh) scaleX(12.5);
  }
}

@keyframes redX {
  0% {
    -webkit-transform: translate(-200vh, 16vh) rotate(0deg);
            transform: translate(-200vh, 16vh) rotate(0deg);
    background-color: #00519d;
  }
  30% {
    background-color: #00519d;
  }
  40% {
    background-color: #dd1832;
  }
  40% {
    -webkit-transform: translate(16vh, 16vh) rotate(720deg);
            transform: translate(16vh, 16vh) rotate(720deg);
  }
  100% {
    -webkit-transform: translate(23vh, 16vh) scaleX(12.5);
            transform: translate(23vh, 16vh) scaleX(12.5);
  }
}

@-webkit-keyframes whiteY {
  from {
    -webkit-transform: translate(16vh, 0) scaleX(0);
            transform: translate(16vh, 0) scaleX(0);
  }
  to {
    -webkit-transform: translate(16vh, 0) scaleX(2);
            transform: translate(16vh, 0) scaleX(2);
  }
}

@keyframes whiteY {
  from {
    -webkit-transform: translate(16vh, 0) scaleX(0);
            transform: translate(16vh, 0) scaleX(0);
  }
  to {
    -webkit-transform: translate(16vh, 0) scaleX(2);
            transform: translate(16vh, 0) scaleX(2);
  }
}

@-webkit-keyframes whiteX {
  from {
    -webkit-transform: translate(0, 16vh) scaleY(0);
            transform: translate(0, 16vh) scaleY(0);
  }
  to {
    -webkit-transform: translate(0, 16vh) scaleY(2);
            transform: translate(0, 16vh) scaleY(2);
  }
}

@keyframes whiteX {
  from {
    -webkit-transform: translate(0, 16vh) scaleY(0);
            transform: translate(0, 16vh) scaleY(0);
  }
  to {
    -webkit-transform: translate(0, 16vh) scaleY(2);
            transform: translate(0, 16vh) scaleY(2);
  }
}

@-webkit-keyframes blue {
  from {
    -webkit-transform: scale(0, 1);
            transform: scale(0, 1);
  }
  to {
    -webkit-transform: scale(12.5, 1);
            transform: scale(12.5, 1);
  }
}

@keyframes blue {
  from {
    -webkit-transform: scale(0, 1);
            transform: scale(0, 1);
  }
  to {
    -webkit-transform: scale(12.5, 1);
            transform: scale(12.5, 1);
  }
}

